
type Props = {
	data: SectionData;
	children?: React.ReactNode;
}

function Section(props: Props) {
	const { id, logo, title, dates, info, bgColor = '', links } = props.data;
	return <section className={`section ${id}`} style={{
		background: bgColor
		}}>
		<picture className='bgImage'>
			<source srcSet={`./bg/${id}.webp`} type="image/webp" />
			<img alt='' src={`./bg/${id}.jpg`} />
		</picture>
		<div className='wrapper'>
			<div className='text-container'>
				<img src={logo} alt="logo" height="60"/>
				<div className='section-title'>	
					<span>{title}</span>
					<span className='section-dates'>{dates}</span>
				</div>
				<div className='section-info'>{info}</div>
				{links && <div className='section-links'>
					{links.map(({link, title}) => {
						return <a key={title} target="_blank" rel="noreferrer" href={link}>{title}</a>
					})}
				</div>}
			</div>
		</div>
	</section>;
}

type SectionData = {
	id: string;
	logo: string;
	title: string;
	dates: string;
	info: string;
	bgColor?: string;
	links?: LinkData[];
}

type LinkData = {
	title: string;
	link: string;
};

export default Section;
